.lenny{
    font-size:  calc(5vh + 3vw);
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(64,181,93,1) 45%, rgba(0,110,133,1) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    display: flex;
    justify-content: center;
    font-weight: bold;
}

.lenny-wrapper{
    display: flex;
    justify-content: center;
    /* disable selection */
    -webkit-user-select: none;  
    -moz-user-select: none;    
    -ms-user-select: none;      
    user-select: none;
    margin: auto;
}